import { equal } from "assert";

class SelectBox {
    constructor() {
        this.$parent = null;
        this.pending = false;
        this.customSelect = {
            $container: null,
            $display: null,
            $list: null
        };
    }

    init($selectWrapper) {
        this.$parent = $selectWrapper;
        this.createElements();
        this.bindEventHandler();
    }
    

    createElements() {
        let $originSelect = this.$parent.find('select');
        let $originOptions = $originSelect.find('option');
        $originSelect.hide();
        
        this.$parent.append('<div class="select-container"></div>');
        this.customSelect.$container = this.$parent.find('.select-container');
        this.customSelect.$container.append('<div class="select-display"></div>');
        this.customSelect.$container.append('<ul class="select-list"></ul>');

        this.customSelect.$display = this.$parent.find('.select-display');
        this.customSelect.$list = this.$parent.find('.select-list');
        this.customSelect.$list.hide();

        $originOptions.each( (index, option) => {
            this.customSelect.$list.append(`<li class="select-item">${option.value}</li>`)
        })

        this.setDisplayText($originOptions.get(0).value)
    }

    bindEventHandler() {
        this.customSelect.$display.click( (e) => {
            if($(e.currentTarget).hasClass('opened')) {
                $(e.currentTarget).removeClass('opened');
                this.customSelect.$list.hide();
            } else {
                $(e.currentTarget).addClass('opened');
                this.customSelect.$list.show();
            }
        })

        this.customSelect.$list.find('li').click ( (e) => {
            this.setDisplayText($(e.currentTarget).text());
            const index = this.customSelect.$list.find('li').index($(e.currentTarget));
            this.$parent.find('option').eq(index).attr('selected', true);
            this.close();
        })

        $('html').click( (e) => {
            const target = $(e.target)
            if(!target.hasClass('select-container') &&
                !target.hasClass('select-display') &&
                !target.hasClass('select-item')) {
                    this.close();
            }
        })
    }

    setDisplayText(text) {
        this.customSelect.$display.text(text)
    }

    reset() {
        this.$parent.find('option').attr('selected', false);
        this.$parent.find('option').eq(0).attr('selected', true);
        this.setDisplayText(this.$parent.find('option').eq(0).text());
    }

    close() {
        this.customSelect.$display.removeClass('opened')
        this.customSelect.$list.hide();
    }
}

export default SelectBox