import SelectBox from '../../common_ui/SelectBox';

import CommonService from '../../services/common.service';

let uiCustomerType = null;
let $input_detail = null;
$(document).ready( async () => {
    if(!USER_SESSION) location.href = "/login"
    uiCustomerType = new SelectBox();
    $input_detail = $('#input-customer-details');
    const resGetCode = await CommonService.getCode(1);
    if(!resGetCode) {
        alert($.i18n.t('wrng_err_n_try'), () => {
            history.back();
        });
        return;
    }
    resGetCode.data.Codes.map( codeInfo => {
        $('#select-customer-type').append(
            createOption(codeInfo.code, codeInfo.code_name)
        )
    })
    uiCustomerType.init($('#select-customer-type-wrapper'))
    bindEventHandler();
})

const bindEventHandler = () => {
    $('#btn-cancel').click((e) => {
        e.preventDefault();
        history.back();
    });
    $('#btn-confirm').click((e) => {
        e.preventDefault();
        if($input_detail.val().length === 0) {
            alert($.i18n.t("rprt_details"));
            return;
        }
        const code = $('option:selected').attr('data-code');
        CommonService.postContact(code, $input_detail.val())
        .then( res => {
            const { status, message } = res;
            if(status === 200) {
                alert(i18next.t('received_inquiry'));
            } else {
                alert(message)
            }
            $input_detail.val('');
            uiCustomerType.reset();
            return;
        })
    })
}

const createOption = (code, code_name) => {
    return `
    <option data-code=${code}>
    ${i18next.t(`inqury_t_${code}`)}
    </option>
    `
}